.App {
  text-align: center;
  background: #F1F3F8;
  height: 2000px;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.BestStore-Wrap {
  /* width: 220px;
   height: 310px; */
   min-width: 300px;
   width: auto;
   height: auto;
  transition : all 0.1s;
  position: relative;
}
.overlay {
  position: absolute;
  /* width: 220px;
  height: 310px; */
  width: auto;
  height: auto;
  background: linear-gradient(105deg,
  transparent 40%,
  rgba(255, 219, 112, 0.8) 45%,
  rgba(132, 50, 255 ,0.6) 50%,
  transparent 54%);
  filter: brightness(1.1) opacity(0.8);
  mix-blend-mode: color-dodge;
  background-size: 150% 150%;
  background-position: 100%;
  transition: all 0.1s;
}
.col-md-4 {
  background: white;
  border-radius: 50px;
  padding-top: 20px;
  margin-left: 4px;
  margin-right: 4px;
}

